import React, { Component } from "react";
import "./Tarjeta.css";

class Tarjeta extends Component {
  render() {
    return this.props.persona.map((persona) => (
      <div className='container'>
        <div className='boxBody'>
          <div className='new d-flex flex-column align-items-center text-center'>
            <img
              src={persona.photo}
              width='84.53'
              className='rounded-circle'
              alt='none'
            />
            <p>{persona.match} match</p>
            <p>
              {persona.nombre} <i className='fas fa-power-off'></i>
            </p>
            <p>{persona.ciudad}</p>
            <p>{persona.bio}</p>
            <button className='pass'>
              <i className='fas fa-times'> </i> Pass
            </button>
            <button className='short'>
              <i className='fas fa-plus'></i> Shortlist
            </button>
          </div>

          <div className='detail d-flex flex-column'>
            <div className='textbox'>
              {persona.jobs.map((sub) => (
                <div className='bloque'>
                  <p>
                    <span className='showtitle'>{sub.show} </span>
                    <span className='text-blue'> - {sub.showDos}</span>
                    <span className='alignright year'>{sub.year}</span>
                  </p>
                  <p>
                    <span className='show'> {sub.genero}</span>
                    <span className='alignright show'>{sub.productora}</span>
                  </p>
                  <div className='clear'></div>
                </div>
              ))}
              <p className='social'>
                <span>MUTUAL CONNECTIONS</span>
                <img
                  src='https://www.w3schools.com/howto/img_avatar.png'
                  class='rounded-circle'
                  width='21'
                  alt='none'
                />
                <img
                  src='https://www.w3schools.com/howto/img_avatar.png'
                  class='rounded-circle'
                  width='21'
                  alt='none'
                />
                <button type='button' class='plusCon'>
                  <span class='badge'> +2</span>
                </button>

                <span className='alignright'>
                  RELEVANT CREDITS{" "}
                  <button type='button' class='plusCon'>
                    <span class='badge'> +3</span>
                  </button>
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>
    ));
  }
}

export default Tarjeta;
